<template>
  <div>
    <!-- authority -->
    <div class="TopNav">
      <div class="left">
        <el-button-group>
          <el-button
            type="primary"
            size="small"
            @click="handleAdd"
            v-permission="'hoder:policy:add'"
          >
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
          <el-button
            type="primary"
            size="small"
            @click="freshService"
            v-permission="'hoder:policy:add'"
          >
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
        </el-button-group>
      </div>
      <div class="right">
        <!-- form search -->
        <el-form
          :inline="true"
          :model="searchForm"
          ref="searchForm"
          @submit.native.prevent="submitForm"
        >
          <el-form-item label prop="policyName">
            <el-input v-model="searchForm.policyName" style="width: 200px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
            <el-button type="primary" size="small" @click="handleReset">
              <a class="fa fa-refresh" style="color: white;"></a> 
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table ref="myTable" :data="tableData" style="width: 100%;" stripe border>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="policyId" label="ID" width="50"></el-table-column>
      <el-table-column prop="policyName" label="" width="150"></el-table-column>
      <el-table-column prop="dataSource" label="" width="90">
        <template scope="scope">
          <span style="margin-left: 10px">{{lableDataSourceEnum[scope.row.dataSource]}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="" width="180">
        <template scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="tagsList" label="">
        <template scope="scope">
          <el-tag
            size="mini"
            v-for="item in scope.row.tagsList"
            :key="item.tagId"
            type="success"
          >{{item.tagName}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width="350">
        <template scope="scope">
          <el-button-group>
             <el-button size="small" type="success" @click="crowdList(scope.row)">
              
              </el-button>
             <el-button
              size="small"
              type="primary"
              v-permission="'hoder:policy:edit'"
              @click="handleEdit(scope.row)"
            ></el-button>
             <el-button
              size="small"
              type="info"
              v-permission="'hoder:policy:del'"
              @click="del(scope.row)"
            ></el-button>
            <el-button
              size="small"
              type="info"
              v-permission="'hoder:policy:add'"
              @click="freshCache(scope.row)"
            ></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <!-- pagination -->
    <div align="right">
      <pagination
        v-bind:currentpage="currentPage"
        v-bind:pagesize="pageSize"
        v-bind:totalcount="totalCount"
        @handle-size-change="handleSizeChange"
        @handle-current-change="handleCurrentChange"
      ></pagination>
    </div>

    <!---->
    <el-dialog
      :title="title"
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addForm" label-width="100px">
        <el-form-item label="" prop="policyName">
          <el-input size="small" v-model="addForm.policyName" placeholder></el-input>
        </el-form-item>
        <el-form-item label="" prop="dataSource">
          <el-select
            v-model="addForm.dataSource"
            filterable
            placeholder=""
            @change="getTags"
          >
            <el-option label="" value="2"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="conditionTagIds">
          <el-tabs tab-position="top" style="height: 200px;">
            <el-tab-pane
              v-for="item in conditionTagIdsData"
              :label="item.groupName"
              :key="item.groupId"
            >
              <el-checkbox-group v-model="addForm.conditionTagIds" class="checkList">
                <el-checkbox v-for="v in item.child" :label="v.tagId" :key="v.tagId">{{v.tagName}}</el-checkbox>
              </el-checkbox-group>
            </el-tab-pane>
          </el-tabs>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import _ from "lodash";
export default {
  data() {
    return {
      // 
      tableData: [],
      lableDataSourceEnum: {
        1: "",
        2: ""
      },
      //
      criteria: {},
      // 
      searchForm: {
        policyName: ""
      },
      title: "",
      // 
      // editFormVisible: false,// 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      totalCount: 1,
      // 
      start: 1,
      addFormVisible: false,
      conditionTagIdsData: [], //
      tagList: [],
      defaultProps: {
        children: "child",
        label: "groupName"
      },
      // 
      addForm: {
        policyId: "",
        policyName: "",
        dataSource: "2",
        conditionTagIds: []
        // 
      },
      addFormRules: {
        policyName: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    freshService(){
      this.$service.freshService().then((data)=>{
        this.$message({
          type:"info",
          message:data
        })
      })
    },
    freshCache(row){
      this.$service.freshCache({policyId:row.policyId}).then((data)=>{
       this.$message({
          type:"info",
          message:data
        })
      })
    },
    getTags() {
      this.addForm.conditionTagIds= [];
      this.$service
        .policyTagSeach({ dataSource: this.addForm.dataSource, s: "" })
        .then(data => {
          this.conditionTagIdsData = data;
        });
    },
    handleAdd() {
      this.addFormVisible = true;
      this.addForm.policyName = "";
      this.addForm.policyId= "";
      this.title=""
      this.getTags();
    },
    handleEdit(row) {
      this.addFormVisible = true;
       this.title=""
      this.addForm.policyId= row.policyId;
      this.addForm.policyName = row.policyName;
      this.addForm.dataSource=row.dataSource.toString();
      this.getTags();
      this.addForm.conditionTagIds=row.conditionTagIds.split(",").map(function(v,i){return parseInt(v)});
      //row.conditionTagIds.split(",");
    },
    crowdList(row) {
      this.$emit("openCrowdPage", row);
    },
    del(row) {
      var id = row.policyId;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service.policyDel({ policyId: id },"").then(data => {
            this.loadData()
          });
        })
        .catch(() => {
          
        });
    },
    // 
    loadData: function() {
      this.criteria["pageNum"] = this.currentPage;
      this.criteria["pageSize"] = this.pageSize;
      this.$service.policyList(this.criteria).then(data => {
        this.tableData = data.pageInfo.list;
        this.totalCount = data.pageInfo.total;
      });
    },
    // , 1020,val=20
    handleSizeChange: function(val) {
      this.pageSize = val;
      this.loadData();
    },
    // , 12,val=2
    handleCurrentChange: function(val) {
      this.currentPage = val;
      this.loadData();
    },
    // ,
    submitForm: function() {
      var _this = this;
      this.$refs.searchForm.validate(function(result) {
        if (result) {
          _this.criteria = _this.searchForm;
          _this.loadData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    handleReset: function() {
      this.$refs.searchForm.resetFields();
    },
    // 
    handleDetail: function(index, row) {
      var id = row.id;
      // todo: 
    },
    // 
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          let addForm = JSON.stringify(this.addForm);
          addForm = JSON.parse(addForm);
          addForm.conditionTagIds = addForm.conditionTagIds.join(",")
          if(this.addForm.policyId!=""){
          this.$service.policyUpate(addForm,"").then(data => {
            this.loadData();
            this.addFormVisible=false;
          });
          }else{
          this.$service.policyAddSave(addForm,"").then(data => {
            this.loadData();
             this.addFormVisible=false;
          });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    cancelAdd: function() {
      this.addFormVisible = false;
    }
  }
};
</script>
<style lang="stylus" scoped>
.checkList
  >>> .el-checkbox+.el-checkbox
    margin-left: 0px
  >>> .el-checkbox
    min-width 130px
    margin-right 5px
.checkList
  height: 200px
  overflow: auto

</style>
